@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:regular,700);
* {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

strong {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

img {
  margin: 0;
  border: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

td, th, tr {
  vertical-align: top;
}

article, aside, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

a {
  color: inherit;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

/* all svg images by default will be with 100% width */
[src*=".svg"] {
  width: 100%;
}

[class*=svg-] use {
  pointer-events: none;
}

span[class*=svg-] {
  display: inline-block;
}

a, .link {
  outline: none;
}
a, a:link, a:visited, .link, .link:link, .link:visited {
  text-decoration: none;
  cursor: pointer;
}
a.notClickable, .link.notClickable {
  cursor: default;
}
a._inherit-color, .link._inherit-color {
  color: inherit;
}
a._underlined-always, .link._underlined-always {
  text-decoration: underline;
}
p a:hover, a._link-underlined:hover, ._link-underlined a:hover, p .link:hover, .link._link-underlined:hover, ._link-underlined .link:hover {
  text-decoration: underline;
}

