html, body {
  font-family: Roboto;
}

.container-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 26px;
}

.burger-container {
  display: none;
}

.header__nav {
  padding: 35px 180px 35px;
}

.header-menu {
  display: flex;
  justify-content: space-between;
  padding: 17px 0;
  font-size: 11px;
  line-height: 22px;
}
.header-menu-item {
  color: #1A1A1A;
  font-weight: 400;
}
@media (max-width: 1200px) {
  .header-menu {
    padding: 40px 100px 40px;
  }
}
@media (max-width: 995px) {
  .header-menu {
    display: block;
    position: absolute;
    background-color: #f5f5f5;
    width: 180px;
    top: 50px;
    right: 15px;
    padding: 0;
    opacity: 0;
    transition: 0.3s;
  }
  .header-menu.active {
    opacity: 1;
  }
  .header-menu-item {
    padding: 5px 0;
    padding-left: 15px;
  }
}

@media (max-width: 995px) {
  .burger-container {
    display: block;
    position: absolute;
    top: 33px;
    right: 2px;
    transform: translate(-50%, -50%);
  }

  .burger-menu {
    position: relative;
    width: 44px;
    height: 37px;
    background-color: black;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.9s ease-in-out 0s;
  }

  .burger-menu:hover {
    cursor: pointer;
  }

  .burger-menu:hover i {
    display: none;
  }

  .burger-menu:hover span:after {
    color: #fff;
    content: "â•³";
    font-weight: 900;
  }

  .burger-menu__dropdown {
    position: absolute;
    width: 182px;
    left: -138px;
    top: 37px;
    background: #F5F5F5;
    max-height: 0;
    overflow: hidden;
    -webkit-transition: max-height 0.2s ease-in;
    transition: max-height 0.2s ease-in;
  }

  .burger-menu__dropdown .active {
    color: #57A4A4;
  }

  .burger-menu__dropdown div {
    font-size: 11px;
    color: #4E4E4E;
    text-transform: uppercase;
    height: 32px;
    padding-left: 14px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .burger-menu:hover .burger-menu__dropdown {
    max-height: 280px;
  }

  .burger-menu__dropdown div:hover {
    background-color: #E5E5E5;
  }
}
